<template>
  <svg class="progress-ring" viewBox="-25,-25,50,50">
    <circle class="progress-ring-circle" r="23" />
    <path class="progress-ring-ring" :d="path" />
    <circle class="progress-ring-end" :cx="endX" :cy="endY" r="2" />
    <text alignment-baseline="middle" text-anchor="middle">{{ text }}</text>
  </svg>
</template>

<script>
export default {
  props: {
    value: {
      type: Number,
      default: 0,
    },
    min: {
      type: Number,
      default: 0,
    },
    max: {
      type: Number,
      default: 1,
    },
    text: {
      type: null,
      default: "",
    },
  },
  computed: {
    theta() {
      const frac = (this.value - this.min) / (this.max - this.min) || 0;
      return frac * 2 * Math.PI;
    },
    path() {
      const large = this.theta > Math.PI;
      return `M0,-23 A23,23,0,${large ? 1 : 0},1,${this.endX},${this.endY}`;
    },
    endX() {
      return Math.cos(this.theta - Math.PI * 0.5) * 23;
    },
    endY() {
      return Math.sin(this.theta - Math.PI * 0.5) * 23;
    },
  },
};
</script>

<style lang="scss" scoped>
.progress-ring {
  width: 50px;
  height: 50px;
}

.progress-ring-circle {
  stroke: rgba(0, 0, 0, 0.1);
  stroke-width: 1;
  fill: none;
}

.progress-ring-ring {
  stroke: #007fff;
  stroke-width: 2;
  fill: none;
}

.progress-ring-end {
  fill: #007fff;
}
</style>